<template>
    <div id="slide">
        <div class="contenedor">
            <div id="carouselExampleFade" class="carousel slide carousel-fade" data-ride="carousel">
                <!-- Indicadores -->
                <ol class="carousel-indicators">
                    <li data-target="#carouselExampleFade" 
                        v-for="image of images" 
                        :key="image.id" v-bind:data-slide-to="(image.id - 1)" 
                        :class="{ active: (image.id=='1') }"
                        >{{image.id}} 
                    </li>
                </ol>
                <!-- Imagenes -->
                <div class="carousel-inner">
                    <div class="carousel-item" v-for="image of images" :key="image.id" :class="{ active: (image.id=='1') }">
                        <img :src="image.location" class="d-block w-100" alt="...">
                        <div class="carousel-caption d-none d-md-block">
                            <h5 class="slide-titulo">{{image.title}}</h5>
                            <div>
                                <p class="slide-descrip">{{image.description}}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Controles siguiente-anterior -->
                <a class="carousel-control-prev" href="#carouselExampleFade" role="button" data-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="sr-only">Previous</span>
                </a>
                <a class="carousel-control-next" href="#carouselExampleFade" role="button" data-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="sr-only">Next</span>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Slide',
    data() {
        return {
            images: [
                {
                    id: '1',
                    title: 'Jazz show',
                    description: 'Una descripcion para el show que va a venir.',
                    location: require("@/assets/img/slide/slide1.jpg" )
                },
                {
                    id: '2',
                    title: 'Music list',
                    description: 'Otra descripcion mas de lo que sera el show.',
                    location: require("@/assets/img/slide/slide2.jpg" )
                },
                {
                    id: '3',
                    title: 'Pop and more',
                    description: 'No te pierdas el show, entradas exclusivas.',
                    location: require("@/assets/img/slide/slide3.jpg" )
                }
            ] 
        }
    }
}
</script>

