<template>
    <div style="position:relative">
        <!-- Titulo de la seccion -->
        <h3 class="titulo-principal">{{titulo}}</h3>
        <!-- Contenedor principal -->
        <div class="carrousel-container">
            <div class="carrousel" 
            :id="identificador" 
            :style="{transform: 'translateX('+ -((tama√±oImg * indice) - 55) +'px)'}"
            @transitionend="loop()"
            @touchstart="touchOn()"
            @touchend="touchOff()">
                <!-- Primer item (no tiene interacciones) -->
                <div class="item">
                    <img :src="shows[shows.length - 1].imagen" alt="">
                    <div class="titulo">{{shows[shows.length - 1].titulo}}</div>
                    <div class="info-top">
                        <p class="online" v-if="shows[shows.length - 1].online"><span class="online-circulo"></span> En vivo</p>
                        <p class="fecha" v-if="!(shows[shows.length - 1].online) && !(shows[shows.length - 1].agotado)">{{shows[shows.length - 1].fecha}}</p>
                        <p class="agotado" v-if="shows[shows.length - 1].agotado">Agotado</p>
                    </div>
                </div>
                <!-- Primer fila de la lista de items -->
                <div class="item" v-for="concierto in shows" :key="concierto.id + '-1'" 
                :class="{ oculto: carrouselInactivo }"
                :id="(identificador+'-1-'+concierto.id)"
                @click="abrirDescripcion(identificador+'-1-'+concierto.id,identificador + 'video1-' + concierto.id,concierto)" 
                @mouseover="hoverOn(identificador+'-1-'+concierto.id,identificador + 'video1-' + concierto.id)" 
                @mouseleave="hoverOff(identificador+'-1-'+concierto.id,identificador + 'video1-' + concierto.id)">
                    <img :src="concierto.imagen" alt="" class="imagen" :class="{ oculto: carrouselInactivo }">
                    <div class="titulo">{{concierto.titulo}}</div>
                    <video v-bind:id="(identificador + 'video1-' + concierto.id)" muted loop>
                        <source :src="concierto.video" type="video/mp4">
                    </video>
                    <img src="@/assets/down-arrow.svg" alt="" class="arrow-down">
                    <div class="info-top">
                        <p class="online" v-if="concierto.online"><span class="online-circulo"></span> En vivo</p>
                        <p class="fecha" v-if="!(concierto.online) && !(concierto.agotado)">{{concierto.fecha}}</p>
                        <p class="agotado" v-if="concierto.agotado">Agotado</p>
                    </div>
                </div>
                <!-- Segunda fila de la lista de items -->
                <div class="item" v-for="concierto in shows" :key="concierto.id + '-2'" 
                v-bind:id="(identificador+'-2-'+concierto.id)"
                @click="abrirDescripcion(identificador+'-2-'+concierto.id,identificador+'video2-' + concierto.id,concierto)"
                @mouseover="hoverOn(identificador+'-2-'+concierto.id,identificador+'video2-' + concierto.id)" 
                @mouseleave="hoverOff(identificador+'-2-'+concierto.id,identificador+'video2-' + concierto.id)">
                    <img :src="concierto.imagen" alt="" class="imagen">
                    <div class="titulo">{{concierto.titulo}}</div>
                    <video v-bind:id="(identificador+'video2-' + concierto.id)" muted loop>
                        <source :src="concierto.video" type="video/mp4">
                    </video>
                    <img src="@/assets/down-arrow.svg" alt="" class="arrow-down">
                    <div class="info-top">
                        <p class="online" v-if="concierto.online"><span class="online-circulo"></span> En vivo</p>
                        <p class="fecha" v-if="!(concierto.online) && !(concierto.agotado)">{{concierto.fecha}}</p>
                        <p class="agotado" v-if="concierto.agotado">Agotado</p>
                    </div>
                </div>
                <!-- Tercer fila de la lista de items -->
                <div class="item" v-for="concierto in shows" :key="concierto.id + '-3'" :class="{ oculto: noInfinito }" 
                v-bind:id="(identificador+'-3-'+concierto.id)"
                @click="abrirDescripcion(identificador+'-3-'+concierto.id,identificador+'video3-' + concierto.id,concierto)"
                @mouseover="hoverOn(identificador+'-3-'+concierto.id,identificador+'video3-' + concierto.id)" 
                @mouseleave="hoverOff(identificador+'-3-'+concierto.id,identificador+'video3-' + concierto.id)">
                    <img :src="concierto.imagen" alt="" class="imagen" :class="{ oculto: noInfinito }">
                    <div class="titulo">{{concierto.titulo}}</div>
                    <video v-bind:id="(identificador+'video3-' + concierto.id)" muted loop>
                        <source :src="concierto.video" type="video/mp4">
                    </video>
                    <img src="@/assets/down-arrow.svg" alt="" class="arrow-down">
                    <div class="info-top">
                        <p class="online" v-if="concierto.online"><span class="online-circulo"></span> En vivo</p>
                        <p class="fecha" v-if="!(concierto.online) && !(concierto.agotado)">{{concierto.fecha}}</p>
                        <p class="agotado" v-if="concierto.agotado">Agotado</p>
                    </div>
                </div>
                <!-- Ultimo item (no tiene interacciones) -->
                <div class="item primerImg" :class="{ oculto: noInfinito }">
                    <img :src="shows[0].imagen" alt="" class="imagen" :class="{ oculto: noInfinito }">
                    <div class="titulo">{{shows[0].titulo}}</div>
                    <div class="info-top">
                        <p class="online" v-if="shows[0].online"><span class="online-circulo"></span> En vivo</p>
                        <p class="fecha" v-if="!(shows[0].online) && !(shows[0].agotado)">{{shows[0].fecha}}</p>
                        <p class="agotado" v-if="shows[0].agotado">Agotado</p>
                    </div>
                </div>
            </div>
            <!-- Controladores anterior/siguiente -->
            <div class="anterior" 
            @click="anterior()"
            :class="{ oculto: carrouselInactivo }">
                <img src="@/assets/left-arrow.svg" alt="">
            </div>
            <div class="siguiente" 
            @click="siguiente()"
            :class="{ oculto: noInfinito }">
                <img src="@/assets/right-arrow.svg" alt="">
            </div>
        </div>
        <!-- Descripcion show -->
        <div class="descripcion-show" 
        v-bind:id="identificador+'-descripcion'" 
        :class="{'descripcion-activa': descripcionActiva}">
            <div class="botton-cerrar" 
            @click="cerrarDescripcion()">
                <div class="linea1"></div>
                <div class="linea2"></div>
            </div>
            <div class="contenedor-video">
                <video v-bind:id="('videoDescripcion')" :key="showEnDescripcion.id" muted autoplay loop>
                    <source :src="showEnDescripcion.video" type="video/mp4">
                </video>
            </div>
            <div class="datos-show">
                <h2>{{showEnDescripcion.titulo}} - <span>{{showEnDescripcion.fecha}}</span></h2>
                <p>{{showEnDescripcion.descripcion}}</p>
                <router-link :to="{ name:'Stream', params:{tituloShow: showEnDescripcion.titulo, show: showEnDescripcion} }" class="btn-comprar">Ver mas</router-link>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
    .titulo-principal {
        margin-bottom: 240px;
        margin-left: 60px;
    }
    .carrousel-container {
        width: calc(100%);
        height: 300px;
        display: flex;
        align-items: center;
        overflow-x: hidden;
        position: absolute;
        top: 0;
        .carrousel {
            height: 190px;
            display:flex;
            align-items: center;
            transition: all 0.3s ease-in-out; 
            z-index: 999;
            .item {
                position: relative;
                height: 100%;
                margin: 0 1px;
                width: calc((100vw - 12px - 17px - 120px)/5);
                overflow: hidden;
                cursor:pointer;
                transition: all 0.3s ease-in-out;
                background-color: rgba(0,0,0,0);
                border-radius: 5px;
                .imagen {
                    height: 100%;
                    width: 100%;
                    transition: all 0.3s ease-in-out;
                    opacity: 1;
                    visibility: visible;
                }
                .titulo {
                    position: absolute;
                    left: 0px;
                    bottom: 0px;
                    font-size: 14px;
                    background-color:#6f6f6e;
                    color: white;
                    width: 100%;
                    padding: 2px 8px;
                    z-index: 99;
                    font-family: Source-Code-Variable;
                }
                video {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    width: 100%;
                    opacity: 0;
                    visibility: hidden;
                    transition: all 0.3s ease-in-out;
                    transition-delay: 0;
                }
                .arrow-down {
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: 7px;
                    height: 25px;
                    width: 25px;
                    opacity: 0;
                    visibility: hidden;
                    transition: all 0.2s ease-in-out;
                }
                .info-top {
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    display: flex;
                    align-items: center;
                }
                .fecha {
                    color: white;
                    font-size: 11px;
                    display: flex;
                    align-items: center;
                    background: rgb(222 127 113);
                    padding: 2px 6px;
                    border-radius: 6px;
                }
                .agotado {
                    color: white;
                    font-size: 11px;
                    display: flex;
                    align-items: center;
                    background: #6f6f6e;
                    padding: 2px 6px;
                    border-radius: 6px;
                }
                .online {
                    position: relative;
                    color: white;
                    font-size: 11px;
                    display: flex;
                    align-items: center;
                    background: rgba(0,0,0,0.8);
                    padding: 2px 6px;
                    border-radius: 6px;
                    margin-right: 6px;
                }
                .online-circulo {
                    height: 11px;
                    width: 11px;
                    border-radius: 50%;
                    background-color: red;
                    margin-right: 2px;
                    margin-bottom: 2px;
                }
            }
            .itemhover {
                width: calc(calc((100vw - 17px - 12px - 120px)/5) + 60px);
                height: 300px;
                padding: 0;
                margin-left: 2px;
                background-color: rgba(0,0,0,1);
                .imagen {
                    width: 100%;
                    opacity: 0;
                    visibility: hidden;
                }
                .titulo {
                    background: none;
                    bottom: 100%;
                    transform: translateY(calc(100% + 3px));
                }
                video {
                    opacity: 1;
                    visibility: visible;
                    transition-delay: 0.6s;
                }
                .arrow-down {
                    opacity: 0.5;
                    visibility: visible;
                    transition-delay: 0.3s;
                }
                .arrow-down:hover {
                    opacity: 1;
                }
            }
        }
        .anterior {
            position: absolute;
            height: 200px;
            width: 90px;
            display: flex;
            align-items: center;
            padding-left: 10px;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
            z-index: 1000;
            img {
                width: 35px;
                height: 35px;
                opacity: 0.7;
                transition: all 0.3s;
                border-radius: 50%;
                padding: 8px 7px 8px 4px;
                background: rgba(0,0,0,.7);
            }
        }
        .siguiente {
            position: absolute;
            height: 200px;
            width: 90px;
            display: flex;
            align-items: center;
            padding-right: 10px;
            justify-content: flex-end;
            top: 50%;
            transform: translateY(-50%);
            right: 0px;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
            z-index: 1000;
            img {
                width: 35px;
                height: 35px;
                opacity: 0.7;
                transition: all 0.3s;
                border-radius: 50%;
                padding: 8px 4px 8px 7px;
                background: rgba(0,0,0,.7);
            }
        }
        .anterior:hover, .siguiente:hover {
            img {
                opacity: 1;
            }
        }
    }
    .descripcion-show {
        position: relative;
        top: -26px;
        height: 0px;
        width: 100%;
        background-color: black;
        transition: all 0.3s ease-in-out;
        transition-delay: 0s;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        overflow: hidden;
        .botton-cerrar {
            position: absolute;
            top: 10px;
            right: 10px;
            padding: 16px 10px;
            cursor: pointer;
            .linea1, .linea2 {
                width: 20px;
                height: 3px;
                border-radius: 10px;
                background-color: white;
            }
            .linea1 { transform: translate(0px,3px) rotate(45deg); }
            .linea2 { transform: translate(0px,0px) rotate(-45deg); }
        }
        .contenedor-video {
            width: 59vw;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            position: relative;
            video {
                width: 100%;
            }
        }
        .contenedor-video::after {
            content: '';
            position: absolute;
            height: 100%;
            width: 40%;
            top: 0;
            right: 0;
            background: linear-gradient(to left, #000,rgba(0,0,0,0.5), rgba(0,0,0,0));
        }
        .datos-show {
            height: 100%;
            color: white;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 40vw;
            h2 {
                background-color: #de7f71;
                padding: 8px 10px;
                border-radius: 3px;
                margin-bottom: 15px;
                font-size: 22px;
                font-family: Source-Code-Variable;
                span {
                    font-family: Source-Code-Variable;
                }
            }
            p {
                max-width: 80%;
            }
            .btn-comprar {
                background-color: #0f940f;
                padding: 6px 25px;
                border: none;
                color: white;
                border-radius: 7px;
                outline: none;
                margin-top: 15px;
            }
            .btn-comprar:hover {
                background-color: #10af10;
                text-decoration: none;
            }
        }
    }
    .descripcion-activa {
        height: 500px;
        transition-delay: 0.3s !important;
    }
    .oculto {
        opacity: 0 !important;
        visibility: hidden !important;
    }
    @media (max-width: 1200px) {
        .carrousel-container {
            .carrousel {
                .item {
                    width: calc((100vw - 120px)/4);
                }
                .itemhover {
                    width: calc(calc((100vw - 120px)/4) + 120px);
                }
            }
        }
    }
    @media (max-width: 1000px) {
        .carrousel-container {
            .carrousel {
                .item {
                    width: calc((100vw - 120px)/3);
                }
                .itemhover {
                    width: calc(calc((100vw - 120px)/3) + 120px);
                }
            }
        }
    }
    @media (max-width: 800px) {
        .carrousel-container {
            .carrousel {
                .item {
                    width: calc((100vw - 120px)/2);
                }
                .itemhover {
                    width: calc(calc((100vw - 120px)/2) + 120px);
                }
            }
        }
        .descripcion-show {
            .contenedor-video {
                display: none;
            }
            .datos-show {
                width: 80vw;
                p {
                    text-align: center;
                    margin: 30px 0;
                }
            }
        }
        .descripcion-activa {
            height: 400px;
        }
    }
    @media (max-width: 650px) {
        .titulo-principal {
            margin-left: 15px;
        }
    }
    @media (max-width: 600px) {
        .carrousel-container {
            .carrousel {
                .item {
                    width: calc((100vw - 120px)/1);
                }
                .itemhover {
                    width: calc(calc((100vw - 120px)/1) + 120px);
                }
            }
        }
    }
</style>

<script>
export default {
    name: 'carrousel',
    props: {
        titulo: String,
        identificador: String,
        shows: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            indice : 1,
            tama√±oImg : 0,
            hover : 0,
            descripcionActiva: false,
            cantidadImagenes: 0,
            showEnDescripcion: this.shows[0],
            carrouselInactivo: true,
            noInfinito: false,
            touchStart: 0,
            touchEnd: 0
        }
    },
    methods: {
        siguiente: function () {
            if (this.indice >= ((this.cantidadImagenes+1) + this.cantidadImagenes)) return;
            document.querySelector('#'+this.identificador).style.transition = 'all 0.5s ease-in-out';
            this.indice += 1;
            this.carrouselInactivo = false;
        },
        anterior: function () {
            if (this.indice <= 1) return;
            document.querySelector('#'+this.identificador).style.transition = 'all 0.5s ease-in-out';
            this.indice -= 1;
        },
        hoverOn: function (itemid,videoid) {
            if (!this.descripcionActiva) {
                document.querySelector('#'+this.identificador).style.transition = 'all 0.5s ease-in-out';
                this.hover = 60;
                var vid = document.getElementById(videoid);
                // vid.play();
                var item = document.querySelector('#'+itemid);
                item.classList.add('itemhover');
                // Show loading animation.
                var playPromise = vid.play();
                if (playPromise !== undefined) {
                    playPromise.then(_ => {
                    })
                    .catch(error => {
                    });
                }
            }
        },
        hoverOff: function (itemid,videoid) {
            document.querySelector('#'+this.identificador).style.transition = 'all 0.5s ease-in-out';
            this.hover = 0;
            var vid = document.getElementById(videoid);
            vid.pause();
            var item = document.getElementById(itemid);
            item.classList.remove('itemhover');
        },
        loop: function () {
            if (this.indice == 1) {
                document.querySelector('#'+this.identificador).style.transition = 'none';
                this.indice = this.cantidadImagenes + 1;
            }
            if (this.indice >= ((this.cantidadImagenes+1) + this.cantidadImagenes)) {
                document.querySelector('#'+this.identificador).style.transition = 'none';
                this.indice = this.cantidadImagenes + 1;
            }
        },
        abrirDescripcion: function (itemid,videoid,show) {
            this.descripcionActiva = true;
            this.hoverOff(itemid,videoid);
            this.showEnDescripcion = show;
        },
        cerrarDescripcion: function () {
            this.descripcionActiva = false;
        },
        ajustarTama√±o: function () {
            let img = document.querySelector('.primerImg');
            if (img != null) {
                this.tama√±oImg = img.clientWidth + 2;
            }
        },
        ajustarCantidadImg: function () {
            let cantImg = document.querySelectorAll('#'+this.identificador+' .item');
            if (cantImg != null) {
                this.cantidadImagenes = (cantImg.length - 2) / 3
            }
        },
        ajustarCarrouselActivo: function () {
            let body = document.querySelector('body');
            if ( (body.clientWidth <= 800) && ( !this.noInfinito ) ) {
                this.carrouselInactivo = false
            }
        },
        ajustarLoop: function () {
            let body = document.querySelector('body');
            if ( (body.clientWidth > 1200) && (this.cantidadImagenes < 5) ) {
                this.noInfinito = true;
            }
            if ( (body.clientWidth <= 1200) && (this.cantidadImagenes < 4) ) {
                this.noInfinito = true;
            }
            if ( (body.clientWidth <= 1000) && (this.cantidadImagenes >= 3) ) {
                this.noInfinito = false;
            }
            if ( (body.clientWidth <= 1000) && (this.cantidadImagenes < 3) ) {
                this.noInfinito = true;
            }
            if ( (body.clientWidth <= 800) && (this.cantidadImagenes >= 2) ) {
                this.noInfinito = false;
            }
            if ( (body.clientWidth <= 800) && (this.cantidadImagenes < 2) ) {
                this.noInfinito = true;
            }
        },
        touchOn: function () {
            this.touchStart = event.changedTouches[0].screenX;
        },
        touchOff: function () {
            this.touchEnd = event.changedTouches[0].screenX;
            let x = this.touchEnd - this.touchStart;
            if ( ( x < -0 ) && (!this.noInfinito) ){
                this.siguiente();
            }
            if ( (x > 0) && (!this.carrouselInactivo) && (!this.noInfinito) ) { //carrouselInactivo: true
                this.anterior();
            }
        }
    },
    mounted() {
        this.ajustarTama√±o();
        this.ajustarCantidadImg();
        this.ajustarLoop();
        this.ajustarCarrouselActivo();
        let body = document.querySelector('body');
        body.onresize = () => {
            let allCarrousel = document.querySelectorAll('.carrousel');
            allCarrousel.forEach(element => {
                element.style.transition = 'all 0.5s ease-in-out';
                element.style.transform = 'translateX(-34%)';
            });
        }
    },
    updated() {
        this.ajustarTama√±o();
        this.ajustarCantidadImg();
        this.ajustarCarrouselActivo();
    }
}

</script>